@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed');

:root {
  --diameter: 90vmin;
  --innerDiameter: calc(var(--diameter) - 8vmin);
  /*---Divisor determines the height/width of the arrow---*/
  --divisor: 15;
  /*---Calculate arrow dimensions this way to keep everything responsive---*/
  --arrowDimensions: calc(var(--diameter) / var(--divisor));
  /*---Value for transform-origin on Y axis---*/
  --originY: calc(var(--arrowDimensions) * var(--divisor) / 2);

  --fontFamily: 'Roboto Condensed', sans-serif;
  --fontWeight: 100;
  --largeFont: calc(var(--diameter) / 6);
  --mediumFont: calc(var(--diameter) / 11);
  --smallFont: calc(var(--diameter) / 20);

  --textColor: #fff;
  --altTextColor: #f00;
  --discColor: #0390b8d9;
  --altDiscColor: tomato;
  /*---Below allows adding transparency if needed
    SOLID -> rgb(var(--windColor)), TRANSPARENT -> rgba(var(--windColor))---*/
  --windColor: 1, 83, 106;

  /*---Grid always seems to cause scrolling????---*/
  overflow: hidden;
}

/*---Loading screen-----------*/
.loading {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.loading h1 {
  font-family: var(--fontFamily);
  font-weight: 100;
  color: #bdc9e9;
  -webkit-animation: loading 2100ms infinite;
          animation: loading 2100ms infinite;
  letter-spacing: 10px;
  font-size: 10vmin;
}

/*---I need to learn sass!-----------------------*/
.loading h1:nth-of-type(1) {
  -webkit-animation-delay: 200ms;
          animation-delay: 200ms;
}
.loading h1:nth-of-type(2) {
  -webkit-animation-delay: 400ms;
          animation-delay: 400ms;
}
.loading h1:nth-of-type(3) {
  -webkit-animation-delay: 600ms;
          animation-delay: 600ms;
}
.loading h1:nth-of-type(4) {
  -webkit-animation-delay: 800ms;
          animation-delay: 800ms;
}
.loading h1:nth-of-type(5) {
  -webkit-animation-delay: 1000ms;
          animation-delay: 1000ms;
}
.loading h1:nth-of-type(6) {
  -webkit-animation-delay: 1200ms;
          animation-delay: 1200ms;
}
.loading h1:nth-of-type(7) {
  -webkit-animation-delay: 1400ms;
          animation-delay: 1400ms;
}
.loading h1:nth-of-type(8) {
  -webkit-animation-delay: 1600ms;
          animation-delay: 1600ms;
}
.loading h1:nth-of-type(9) {
  -webkit-animation-delay: 1800ms;
          animation-delay: 1800ms;
}
.loading h1:nth-of-type(10) {
  -webkit-animation-delay: 2000ms;
          animation-delay: 2000ms;
}

@-webkit-keyframes loading {
  0% { color: #bdc9e9; }
  50% { color: var(--discColor); }
  100% { color: #bdc9e9; }
}

@keyframes loading {
  0% { color: #bdc9e9; }
  50% { color: var(--discColor); }
  100% { color: #bdc9e9; }
}

/*---------End of loading screen----------------*/

.app {
  width: 100vw;
  height: 100vh;
  display: grid;
  /*grid-template-rows: auto 1fr auto;*/
  /*---Had to change this as something wasnt working in chrome---*/
  grid-template-rows: 1fr 1fr 1fr;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  color: var(--discColor);
  font-family: var(--fontFamily);
  font-weight: var(--fontWeight);
}

.bg-image {
  height: 100vh;
  width: 100vw;
  background: url('assets/feather.jpeg');
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-filter: brightness(95%);
          filter: brightness(95%);
}

button {
  color: var(--textColor);
  font-size: var(--smallFont);
  background: -webkit-radial-gradient(top left, circle, rgb(56, 195, 178, 0.8), var(--discColor));
  background: -o-radial-gradient(top left, circle, rgb(56, 195, 178, 0.8), var(--discColor));
  background: radial-gradient(circle at top left, rgb(56, 195, 178, 0.8), var(--discColor));
  -webkit-box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
          box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  border: none;
  border-radius: 50%;
  width: 9vmin;
  height: 9vmin;
  cursor: pointer;
  position: absolute;
  top: calc(var(--diameter) / 15);
  right: calc(var(--diameter) / 15);
  z-index: 99;
}
button:active {
  opacity: 0.7;
  -webkit-transform: translate(0px, 1px);
      -ms-transform: translate(0px, 1px);
          transform: translate(0px, 1px);
}

.city {
  grid-row: 1;
  grid-column: 1;
  font-weight: var(--fontWeight);
  font-size: var(--mediumFont);
  z-index: 9;
}

.weather-container {
  width: var(--diameter);
  height: var(--diameter);
  border-radius: 50%;
  align-self: center;
  display: grid;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: -webkit-radial-gradient(top left, circle, rgb(56, 195, 178, 0.8), var(--discColor));
  background: -o-radial-gradient(top left, circle, rgb(56, 195, 178, 0.8), var(--discColor));
  background: radial-gradient(circle at top left, rgb(56, 195, 178, 0.8), var(--discColor));
  -webkit-box-shadow: 2px 2px 2px rgba(0,0,0,0.5);
          box-shadow: 2px 2px 2px rgba(0,0,0,0.5);
  overflow: hidden;
  color: var(--textColor);
  opacity: 0;
  -webkit-animation: fadeIn 500ms forwards;
          animation: fadeIn 500ms forwards;
  -webkit-animation-delay: 300ms;
          animation-delay: 300ms;
  -webkit-animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
          animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
  position: relative;
}

.points {
  position: absolute;
  font-size: 3vmin;
  color: var(--textColor);
}

.north {
  justify-self: center;
  -ms-flex-item-align: start;
      align-self: flex-start;
  top: 0.5vmin;
}

.east {
  justify-self: flex-end;
  -ms-flex-item-align: center;
      align-self: center;
  right: 1.2vmin;
}

.south {
  justify-self: center;
  -ms-flex-item-align: end;
      align-self: flex-end;
  bottom: 0;
}

.west {
  left: 0.8vmin;
}

.angled-lines {
  position: absolute;
  background: var(--textColor);
  width: calc((var(--diameter) - var(--innerDiameter)) / 2);
  height: 1px;
  --origin-left: calc(-1 * var(--innerDiameter) / 2);
}

.angled-lines:nth-of-type(1) {
  right: 0;
  -webkit-transform-origin: var(--origin-left) 0;
      -ms-transform-origin: var(--origin-left) 0;
          transform-origin: var(--origin-left) 0;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.angled-lines:nth-of-type(2) {
  right: 0;
  -webkit-transform-origin: var(--origin-left) 0;
      -ms-transform-origin: var(--origin-left) 0;
          transform-origin: var(--origin-left) 0;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.angled-lines:nth-of-type(3) {
  right: 0;
  -webkit-transform-origin: var(--origin-left) 0;
      -ms-transform-origin: var(--origin-left) 0;
          transform-origin: var(--origin-left) 0;
  -webkit-transform: rotate(135deg);
      -ms-transform: rotate(135deg);
          transform: rotate(135deg);
}

.angled-lines:nth-of-type(4) {
  right: 0;
  -webkit-transform-origin: var(--origin-left) 0;
      -ms-transform-origin: var(--origin-left) 0;
          transform-origin: var(--origin-left) 0;
  -webkit-transform: rotate(225deg);
      -ms-transform: rotate(225deg);
          transform: rotate(225deg);
}

@-webkit-keyframes fadeIn {
  0% { opacity: 0; top: 200px; }
  100% { opacity: 1; top: 0; }
}

@keyframes fadeIn {
  0% { opacity: 0; top: 200px; }
  100% { opacity: 1; top: 0; }
}

.weather-inner {
  width: var(--innerDiameter);
  height: var(--innerDiameter);
  border: 1px solid var(--textColor);
  border-radius: 50%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  -ms-flex-align: center;
      align-items: center;
}

/*---font-weight doesnt cascade seemingly???---*/
.weather-inner * {
  font-weight: var(--fontWeight);
}

.main-temp {
  grid-column: 1 / -1;
  grid-row: 1 / 3;
  font-size: var(--largeFont);
  cursor: pointer;
  padding: 1vmin;
  -webkit-transition: 100ms;
  -o-transition: 100ms;
  transition: 100ms;
  justify-self: center;
  border-radius: 1vmin;
}

.min {
  grid-column: 1 / 3;
  grid-row: 3;
  font-size: var(--smallFont);
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding-top: calc(var(--diameter) / 35);
}

hr {
  margin: 0 2vmin;
  height: 1px;
  border: none;
  background: var(--textColor);
}

.left-hr {
  grid-column: 1 / 3;
  grid-row: 3;
}

.sunrise {
  grid-column: 1 / 3;
  grid-row: 3;
  font-size: var(--smallFont);
  -ms-flex-item-align: end;
      align-self: flex-end;
  padding-bottom: calc(var(--diameter) / 35);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.sunrise h3 {
  font-size: var(--smallFont);
  margin-right: 5px;
}

.max {
  grid-column: 4 / -1;
  grid-row: 3;
  font-size: var(--smallFont);
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding-top: calc(var(--diameter) / 35);
}

.right-hr {
  grid-column: 4 / -1;
  grid-row: 3;
}

.sunset {
  grid-column: 4 / -1;
  grid-row: 3;
  font-size: var(--smallFont);
  -ms-flex-item-align: end;
      align-self: flex-end;
  padding-bottom: calc(var(--diameter) / 35);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.sunset h3 {
  font-size: var(--smallFont);
  margin-right: 5px;
}

.svg-icon {
  grid-column: 3 / 4;
  grid-row: 3;
  justify-self: center;
  width: calc(var(--diameter) / 4.5);
}
/*-----Rain animation----*/
.raindrop {
  -webkit-animation: rain 2s infinite;
          animation: rain 2s infinite;
}
.raindrop:nth-child(2) {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.raindrop:nth-child(3) {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.raindrop:nth-child(4) {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
@-webkit-keyframes rain {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}
@keyframes rain {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

/*-----Snow animation----*/
.flake {
  position: relative;
  -webkit-animation: snow 2s infinite ease-out;
          animation: snow 2s infinite ease-out;
}
.flake2 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.flake3 { -webkit-animation-delay: 1s; animation-delay: 1s; }
.flake4 { -webkit-animation-delay: 1.5s; animation-delay: 1.5s; }

@-webkit-keyframes snow {
  0% { top: 0; left: 0; opacity: 0; }
  20% { left: 3px; opacity: 1;}
  40% { left: -3px; }
  60% { left: 3px; }
  100% { top: 10px; left: 3px; opacity: 0; }
}

@keyframes snow {
  0% { top: 0; left: 0; opacity: 0; }
  20% { left: 3px; opacity: 1;}
  40% { left: -3px; }
  60% { left: 3px; }
  100% { top: 10px; left: 3px; opacity: 0; }
}

/*-----Fog animation----*/
.fogline {
  -webkit-animation: fog 10s infinite;
          animation: fog 10s infinite;
}
.fog5 {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.fog4 {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
.fog3 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.fog2 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.fog1 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@-webkit-keyframes fog {
  0% { stroke: #fff; }
  50% { stroke: #737474; }
  100% { stroke: #fff; }
}
@keyframes fog {
  0% { stroke: #fff; }
  50% { stroke: #737474; }
  100% { stroke: #fff; }
}

/*---Clear day animation---*/
.sun {
  -webkit-animation: spin 30s infinite linear;
          animation: spin 30s infinite linear;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0); transform: rotate(0); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes spin {
  from { -webkit-transform: rotate(0); transform: rotate(0); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.sunline {
  -webkit-animation: sun 2s infinite;
          animation: sun 2s infinite;
  stroke-dasharray: 12px;
  stroke-dashoffset: 0px;
}
@-webkit-keyframes sun {
  0% { stroke-dashoffset: 0px; }
  50% { stroke-dashoffset: 6px; }
  100% { stroke-dashoffset: 0px; }
}
@keyframes sun {
  0% { stroke-dashoffset: 0px; }
  50% { stroke-dashoffset: 6px; }
  100% { stroke-dashoffset: 0px; }
}

/*---Cloudy animation---*/
.cloud {
  opacity: 0;
  -webkit-animation: clouds 7s infinite;
          animation: clouds 7s infinite;
}
.cloud2 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.cloud3 {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.cloud4 {
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}
@-webkit-keyframes clouds {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}
@keyframes clouds {
  0% { opacity: 0; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

/*---Wind1 animation---*/
.line {
  -webkit-animation: wind1 2.5s infinite;
          animation: wind1 2.5s infinite;
  position: relative;
}
.line2 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.line5, .line6, .line7 {
  -webkit-animation: wind1 2s infinite;
          animation: wind1 2s infinite;
}
.line8, .line9, .line10 {
  -webkit-animation: wind1 1.5s infinite;
          animation: wind1 1.5s infinite;
}
@-webkit-keyframes wind1 {
  0% { -webkit-transform: translateX(0); transform: translateX(0); }
  50% { -webkit-transform: translateX(5px); transform: translateX(5px); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}
@keyframes wind1 {
  0% { -webkit-transform: translateX(0); transform: translateX(0); }
  50% { -webkit-transform: translateX(5px); transform: translateX(5px); }
  100% { -webkit-transform: translateX(0); transform: translateX(0); }
}

/*---Wind2 animation---*/
.windmill {
  -webkit-animation: rotate 2s infinite ;
          animation: rotate 2s infinite ;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
}
@-webkit-keyframes rotate {
  from { -webkit-transform: rotate(0); transform: rotate(0); }
  to { -webkit-transform: rotate(720deg); transform: rotate(720deg); }
}
@keyframes rotate {
  from { -webkit-transform: rotate(0); transform: rotate(0); }
  to { -webkit-transform: rotate(720deg); transform: rotate(720deg); }
}

.mainDescription {
  grid-column: 1 / -1;
  grid-row: 4;
  font-size: var(--mediumFont);
  -ms-flex-item-align: start;
      align-self: flex-start;
}

.detailDescription {
  /*--This can get pretty long ie: "light intensity drizzle rain"---*/
  grid-column: 1 / -1;
  grid-row: 4;
  font-size: var(--smallFont);
  text-align: center;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.wind-speed {
  grid-column: 1 / -1;
  grid-row: 5;
  font-size: calc(var(--diameter) / 13);
  color: rgb(var(--windColor));
}

/*----the arrow(class name generated by styled-components)----------*/
.sc-bdVaJa {
  width: 0;
  height: 0;
  position: absolute;
  border-top: var(--arrowDimensions) solid rgba(var(--windColor), 0.8);
  border-left: var(--arrowDimensions) solid transparent;
  border-right: var(--arrowDimensions) solid transparent;
  top: 0;
  left: 50%;
  z-index: 99;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transform-origin: 50% var(--originY);
      -ms-transform-origin: 50% var(--originY);
          transform-origin: 50% var(--originY);
}

.date {
  font-size: var(--mediumFont);
  font-weight: var(--fontWeight);
  z-index: 9;
  text-shadow: 1px 1px 1px #fff, -1px -1px 1px #fff;
}

@media (orientation: landscape) {
  :root {
    --diameter: 75vmin;
  }
}



